.form {
  text-align: center;
}

.form-preview {
  height: 240px;
  max-width: 500px;
  background-color: #f6f8fa;
  border: 1px solid #d0d7de;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 10px;
  display: flex;
  position: relative;
}

.form-preview__input {
  cursor: pointer;
  height: 100%;
  opacity: 0;
  width: 100%;
  position: absolute;
}

.form-preview .icon {
  height: 50px;
  width: 50px;
}

.form-preview__image {
  height: 100%;
  object-fit: cover;
  width: 100%;
  border-radius: 10px;
}

.form-button {
  text-align: center;
  width: 100%;
  margin: 20px 0;
}

* {
  box-sizing: border-box;
  color: #333;
  margin: 0;
  padding: 0;
  font-family: Roboto Mono, monospace;
}

body {
  background-color: #fffdfa;
}

.container {
  max-width: 850px;
}

.content {
  text-align: center;
  gap: 30px;
  margin: 60px auto;
  display: grid;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #ea4e43;
  border: none;
  border-radius: 5px;
  padding: 10px 50px;
  font-size: 16px;
}

.button:hover {
  background-color: #d73223;
  transition: all .2s;
}

.header {
  height: 60px;
  border-bottom: 2px solid #0003;
  justify-content: space-between;
  align-items: center;
  padding: 0 60px;
  display: flex;
}

.header__title {
  font-size: 18px;
}

.header svg {
  width: 40px;
}

.result {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 5px;
  display: flex;
}

.result__text {
  pointer-events: none;
  width: 100%;
  background-color: #f6f8fa;
  border: 1px solid #d0d7de;
  border-radius: 5px;
  padding: 10px 15px;
}

.result.is-visible {
  opacity: 1;
  visibility: visible;
}

